
html,
body {
  height: 100%;
  padding: 0;
  margin: 0;
}
.flex-container {
  min-height: 100%;
  display: -ms-flexbox;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-box;
  display: box;
  -ms-flex-direction: row;
  -webkit-box-orient: horizontal;
  -ms-box-orient: horizontal;
  box-orient: horizontal;
}
.element-picker {
  background: #2c3e50;
  width: 250px;
  -ms-flex: 0 250px;
  -webkit-box-flex: 0;
  -ms-box-flex: 0;
  box-flex: 0;
}
.main {
  -ms-flex: 1;
  -webkit-box-flex: 1;
  -ms-box-flex: 1;
  box-flex: 1;
}
div.draggable {
  border: 2px #dde4e8 dashed;
  border-radius: 5px;
  min-height: 100px;
}



